    // }

    div {
      width: 110px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    margin: 15px .5%;
    // margin: 15px;
      font-weight: bold;
      font-size: 18px;

      i {
        font-size: 64px;
        font-weight: normal;
      }
    }

    @media screen and (max-width: 1250px ) {
      max-width: 800px;
      div {
        margin: 15px 1.8% 10px 1.8%;
      }

    
    }
    @media (max-width: 760px) {
      // max-width: 500px;
      div {
        margin: 15px 1% 10px 1%;
        height: auto;
      }
    
    } 
    
  }
}
  .home-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .home-text {
      flex-direction: column;
      align-items: center;
      position: relative;
      justify-content: center;
      text-align: left;
      width: auto;
      min-height: calc(100% - 505px);
      font-size: calc(12px + 1.5vh);

      p {
        display: flex;
        margin: 35px 50px;
      }

      .last-p {
        margin-bottom: 10px;
      }
    }
    .home-images {
      width: 80%;
      height: 100%;
      display: flex;
      flex-direction: row;
      margin: 20px 0;
    }
  }

